// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import VueCordova from 'vue-cordova'
import cordovaTest from 'components/cordovaTest/cordovaTest.vue'
import goods from 'components/goods/goods.vue'
import 'common/stylus/index.styl'
import InfiniteScroll from 'vue-infinite-scroll'
import AppConfig from '../config/appconfig'

Vue.use(InfiniteScroll)
Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(VueCordova)
Vue.use(AppConfig)

if (window.location.protocol === 'file:') {
  var cordovaScript = document.createElement('script')
  cordovaScript.setAttribute('type', 'text/javascript')
  cordovaScript.setAttribute('src', 'cordova.js')
  document.body.appendChild(cordovaScript)
}

const contentthree = { template: '<div>第三个页面</div>' }

const routes = [
  { path: '/contentone', component: goods },
  { path: '/contenttwo', component: cordovaTest },
  { path: '/contentthree', component: contentthree }
]

const router = new VueRouter({
  routes
})

router.push('/contentone')

/* eslint-disable no-new */
new Vue({
  router,
  el: '#sellapp',
  template: '<App/>',
  components: { App }
})




